<template>
  <div class="main">
    <div class="crumb_box">
      <div class="search_box">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item>首页</el-breadcrumb-item>
          <el-breadcrumb-item>个人信息</el-breadcrumb-item>
        </el-breadcrumb></div>
    </div>
    <div class="clear"></div>
    <div class="application_item">
      <div class="userMsg" :data="users">
        <div class="userMsgl">
          <div class="userImg"><img :src="users.image" alt="" class="img-circle user-avatar" ><span @click="visible.updateImagepost = true">编辑头像</span></div>
          <div class="userMsgContent">
            <div class="userMsgContentTit"><h3 ><span>{{hoursTip}}，</span>{{users.userName}}</h3><!--<a href="" class="mqy_click_button">高级实名认证&gt;</a>--></div>
            <div class="userMsgContent2">
              <span class="userlist" v-show="users.registerTime != null">注册时间：{{dateFrm(users.registerTime)}}</span>
              <span class="userlist" v-show="users.registerTime == null">注册时间：暂无</span>
              <!--<a href=""  class="makeMsg mqy_click_button">编辑资料</a>-->
            </div>
            <div class="userMsgContent3">
               <span class="phone">
                 <span class="spanCont"> <i class="icon iconfont iconshouji"></i>{{users.mobile}}</span>
                 <span class="changeBtn mqy_click_button"  @click="visible.updatemobilepost = true">[修改]</span>
               </span>
               <span class="safeLevel">
                 <span class="spanCont"> <i class="icon iconfont iconyouxiang"></i>{{users.email}}</span>
                 <span class="changeBtn mqy_click_button" @click="visible.updateemailpost1 = true">[修改]</span>
               </span>
               <span class="loginPassword">
                 <span class="spanCont"> <i class="icon iconfont iconmima"></i>登录密码</span>
                 <span  @click="visible.updatesecretpost = true" class="changeBtn mqy_click_button">[修改]</span>
               </span>
            </div>
          </div>
        </div>
      </div>
      <div class="clear"></div>

    <div class="areawarp">
      <h2 class="title2">基本资料</h2>
      <div class="clear"></div>
      <div class="basic_information" :data="users"  >
        <div class="item_list">
          <label>入职时间：</label>
          <div class="txt txt2" v-show="users.entryTime == null">暂无</div>
          <div class="txt"  v-show="users.entryTime != null">{{dateFrm(users.entryTime)}}</div>
        </div>
        <div class="item_list">
          <label>工号：</label>
          <div class="txt txt2" v-show="users.jobNumber == null">暂无</div>
          <div class="txt" v-show="users.jobNumber != null">{{users.jobNumber }}</div>
        </div>
        <div class="item_list">
          <label>性别：</label>
          <div class="txt txt2" v-show="users.sex == null">暂无</div>
          <div class="txt" v-show="users.sex != null">{{users.sex}}</div>
        </div>
        <div class="item_list">
          <label>部门：</label>
          <div class="txt txt2" v-show="users.departName == null">暂无</div>
          <div class="txt" v-show="users.departName != null">{{users.departName}}</div>
        </div>
        <div class="item_list">
          <label>职位：</label>
          <div class="txt txt2" v-show="users.userPosts.length == 0">暂无</div>
          <div class="txt" v-for="list in users.userPosts"  v-show="users.userPosts.length != 0">
            {{list.userPostname}}
          </div>
        </div>
        <div class="item_list">
          <label>隶属机构：</label>
          <div class="txt txt2" v-show="users.departPath == null">暂无</div>
          <div class="txt" v-show="users.departPath != null">{{users.departPath}}</div>
        </div>
        <div class="item_list">
          <label>角色：</label>
          <div class="txt txt2">暂无</div>
        </div>
      </div>
      <div class="clear"></div>
    </div>
    <div class="clear"></div>

    <div class="areawarp">
      <h2 class="title2">联系方式</h2>
      <div class="clear"></div>
      <div class="basic_information" :data="users">
        <el-form v-model="contactsForm" label-width="93px" class="demo-ruleForm">
          <ul style="margin-left: 20px;    margin-top: 20px;">
            <li v-for="(item, index) in contactsForm.contacts" :key="index" style="margin-top: 10px">
                <span v-model="items[index]" style="float: left">
                    <el-input placeholder="请输入部门"   v-model="item.title" style="width: 120px"></el-input> ：
                    <el-input placeholder="请输入电话号码"   v-model="item.phone" style="width: 200px"></el-input>
                </span>
                <i class="el-icon-remove iconremove"  @click="deleteExceptPrice(index,item.index,item.title)" style="float: left; margin-top: 10px"></i>
                <div class="clear"></div>
            </li>
          </ul>
        </el-form>
        <div class="clear"></div>
        <div style="margin-top:20px; margin-left:150px"><el-button type="text" class="el-icon-plus" @click="addExceptPrice">添加联系方式</el-button></div>
      </div>
      <div class="clear"></div>
    </div>
    <el-button type="primary" @click="onSubmitContacts">保存</el-button>

    <el-dialog title="修改用户名" :visible.sync="visible.updatenamepost" width="500px">
      <el-form :model="UserNameForm"   ref="UserNameForm_rules"  label-width="93px" class="demo-ruleForm">
        <el-form-item label="新用户名：" prop="userName" :rules="[ { required: true, message: '新用户名为空', trigger: 'blur' }]">
          <el-input v-model="UserNameForm.userName"  placeholder="请输入新用户名"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="onSubmitUserName">确 定</el-button>
        <el-button  @click="visible.updatenamepost = false">取 消</el-button>
      </div>
    </el-dialog>

    <el-dialog title="上传头像" :visible.sync="visible.updateImagepost" width="500px" class="touxiang">
      <el-upload class="avatar-uploader"
                 name="image"
                 :show-file-list="false"
                 :action="fileUploadUrl"
                 :headers="requestHeader"
                 :on-success="handleAvatarSuccess"
                 :before-upload="beforeAvatarUpload" style="text-align: center;padding-bottom: 20px">
        <img v-if="imageUrl" :src="imageUrl" class="avatar">
        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
      </el-upload>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="onSubmitImage">确 定</el-button>
        <el-button @click="visible.updateImagepost = false">取 消</el-button>
      </div>
    </el-dialog>

    <el-dialog title="修改密码" :visible.sync="visible.updatesecretpost" width="500px">
        <el-form :model="UserSecretForm" :rules="rules"  ref="UserSecretForm" label-width="93px" class="demo-ruleForm">
          <el-form-item label="原密码：" prop="oldPassword">
            <el-input type="password" placeholder="请输入原密码"  v-model="UserSecretForm.oldPassword" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item  label="新密码：" prop="newPassword">
            <el-input v-model="UserSecretForm.newPassword" @change="changePwd" placeholder="请输入新密码" type="password"></el-input>
          </el-form-item>

          <p class="pwdtxt" style="margin-top: -9px">当前系统密码强度要求为：<span class="s1">{{psdmegtxt}}</span><br/>由单一数字，字母，字符组成。</p>
          <el-form-item label="密码强度：" style="margin-bottom: 5px" >
            <el-slider :step="30" v-model="password.value" style="width: 367px" :show-tooltip="false" ></el-slider>
          </el-form-item>
          <el-form-item label="确认密码：" prop="checkPassword">
            <el-input type="password"  placeholder="请输入确认密码" v-model="UserSecretForm.checkPassword"  autocomplete="off" ></el-input>
          </el-form-item>
          <p class="pwdtxt"  style="margin-top: -9px;margin-bottom: 20px">登录密码（6-50个字符，区分英文大小写）</p>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="onSubmitSecret">确 定</el-button>
          <el-button @click="visible.updatesecretpost = false">取 消</el-button>
        </div>
    </el-dialog>

    <el-dialog :data="users" title="修改手机号码" :visible.sync="visible.updatemobilepost" width="500px">
        <el-form :model="UserOldMobileForm"  :rules="rules"   ref="UserOldMobileForm" label-width="79px" class="demo-ruleForm">
          <p style="margin-bottom: 15px">为了保证您的帐号安全，更换手机前请先进行安全验证</p>
          <el-form-item label="手机：">
            <span style="float:left;">{{users.mobile}}</span>
            <span v-show="sendAuthCode" class="auth_text auth_text_blue" @click="getOldMobileCode" style="margin-left: 10px">获取验证码</span>
            <span v-show="!sendAuthCode" class="auth_text auth_text_blue auth_text_gray" style="margin-left: 10px"> {{auth_time}} S</span>
          </el-form-item>
          <el-form-item label="验证码：" prop="mobileCode"  label-width="79px" >
            <el-input v-model="UserOldMobileForm.mobileCode"  placeholder="请输入验证码"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="validateOldMobileCodes">下一步</el-button>
          <el-button  @click="visible.updatemobilepost = false">取 消</el-button>
        </div>
      </el-dialog>

    <el-dialog title="绑定手机" :visible.sync="visible.updatemobilepost1" width="500px">
        <el-form :model="UserNewMobileForm1" :rules="rules"   ref="UserNewMobileForm1" label-width="79px" class="demo-ruleForm">
          <p style="margin-bottom: 15px">为了保证您的帐号安全，更换手机前请先进行安全验证</p>
          <el-form-item label="手机：" prop="mobile">
            <el-input type="mobile" v-model="UserNewMobileForm1.mobile"  autocomplete="off" style="float:left;width: 273px;margin-right:5px"></el-input>
            <span v-show="sendAuthCode1" class="auth_text auth_text_blue" @click="getNewMobileCode">获取验证码</span>
            <span v-show="!sendAuthCode1" class="auth_text  auth_text_blue auth_text_gray">{{auth_time1}}S</span>
          </el-form-item>
        </el-form>
        <el-form :model="UserNewMobileForm2" :rules="rules"   ref="UserNewMobileForm2" label-width="79px" class="demo-ruleForm">
          <el-form-item label="验证码：" prop="mobileCode">
            <el-input v-model="UserNewMobileForm2.mobileCode" style="width: 381px;"  placeholder="请输入验证码"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="onSubmitMobile">确 定</el-button>
          <el-button  @click="visible.updatemobilepost1 = false">取 消</el-button>
        </div>
      </el-dialog>

    <el-dialog title="绑定邮箱" :visible.sync="visible.updateemailpost" width="500px">
        <el-form :model="UserEmailForm1" :rules="rules"   ref="UserEmailForm1" label-width="79px" class="demo-ruleForm">
          <el-form-item label="邮箱：" prop="email">
            <el-input type="email" placeholder="请输入邮箱" v-model="UserEmailForm1.email"  autocomplete="off" style="float:left;width: 273px;margin-right: 5px">
            </el-input>
            <span v-show="sendAuthCode2" class="auth_text auth_text_blue" @click="getEmailCode">获取验证码</span>
            <span v-show="!sendAuthCode2" class="auth_text auth_text_blue auth_text_gray"> {{auth_time2}}S</span>
          </el-form-item>
        </el-form>
        <el-form :model="UserEmailForm2" :rules="rules"   ref="UserEmailForm2" label-width="79px" class="demo-ruleForm">
          <el-form-item label="验证码：" prop="emailCode">
            <el-input v-model="UserEmailForm2.emailCode"  placeholder="请输入验证码"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="onSubmitEmail">确 定</el-button>
          <el-button @click="visible.updateemailpost = false">取 消</el-button>
        </div>
      </el-dialog>

    <el-dialog title="编辑邮箱" :visible.sync="visible.updateemailpost1" width="500px">
        <el-form :model="UserEmailForm1" :rules="rules"   ref="UserEmailForm1" label-width="79px" class="demo-ruleForm">
          <el-form-item label="邮箱：" prop="email">
            <el-input type="email"  placeholder="请输入邮箱" v-model="UserEmailForm1.email"  autocomplete="off" style="float:left;width: 273px;margin-right: 5px">
            </el-input>
            <span v-show="sendAuthCode2" class="auth_text auth_text_blue" @click="getEmailCode">获取验证码</span>
            <span v-show="!sendAuthCode2" class="auth_text auth_text_blue auth_text_gray"> {{auth_time2}}S</span>
          </el-form-item>
        </el-form>
        <el-form :model="UserEmailForm2" :rules="rules"   ref="UserEmailForm2" label-width="79px" class="demo-ruleForm">
          <el-form-item label="验证码：" prop="emailCode">
            <el-input v-model="UserEmailForm2.emailCode" placeholder="请输入验证码"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="onSubmitEmail">确 定</el-button>
          <el-button @click="visible.updateemailpost1 = false">取 消</el-button>
        </div>
      </el-dialog>

    </div>
  </div>
</template>

<script>
  import {getUserList,updateUserName,getContactList,addContact,deleteContact} from '@/api/UserApi';
  import {updateSecret,sendNewMobileCode,sendOldMobileCode,updateMobile,updateEmail,sendEmailCode,validateOldMobileCode} from '@/api/UserApi';
  import moment from 'moment'
  import Cookies from 'js-cookie';
  export default {
    name: "BasicInformation",
    data () {
      var validatePass = (rule, value, callback) => {
        if (value !== this.UserSecretForm.newPassword) {
          callback(new Error("两次输入密码不一致!"));
        } else {
          callback();
        }
      };

      var validateEmail = (rule, value, callback) => {

        if (value !== '') {
          var reg=/^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
          if(!reg.test(value)){
            callback(new Error('请输入有效的邮箱'));
          }
        }
        callback();
      };

      var validateMobilePhone = (rule, value, callback) => {
        if (value !== '') {
          var reg=/^1[3456789]\d{9}$/;
          if(!reg.test(value)){
            callback(new Error('请输入有效的手机号码'));
          }
        }
        callback();
      };

      return {
        hoursTip:' ',
        inputPwd: null,
        psdmegtxt:"弱",
        sendAuthCode:true,/*布尔值，通过v-show控制显示‘获取按钮'还是‘倒计时' */
        auth_time: 0, /*倒计时 计数器*/
        sendAuthCode1:true,/*布尔值，通过v-show控制显示‘获取按钮'还是‘倒计时' */
        auth_time1: 0, /*倒计时 计数器*/
        sendAuthCode2:true,/*布尔值，通过v-show控制显示‘获取按钮'还是‘倒计时' */
        auth_time2: 0, /*倒计时 计数器*/
        visible:{
          updatesecretpost:false,
          updateemailpost:false,
          updateemailpost1:false,
          updatemobilepost:false,
          updatemobilepost1:false,
          updatenamepost:false,
          updateImagepost:false
        },
        items: [0],
        items2:
           {
             index:0,
             title: "",
             phone: ""
         },
        ele: '',
        users:{
          departId:"",
          departName:"",
          departPath: "",
          email: "",
          entryTime: null,
          image: "",
          jobNumber: null,
          jobStatus: 0,
          mobile: "",
          registerTime: null,
          sex: null,
          userId: "",
          userName: "",
          userPosts:[
            {userPostid:""},
            {userPostname:""}
          ]
        },
        imageUrl:"",
        UserNameForm: {},
        UserImageForm:{},
        contactsForm:{
          contacts:[
            // {
            //   // title: "",
            //   // phone: ""
            // }
          ]
        },
        UserEmailForm1: {
          email:""
        },
        UserEmailForm2: {
          emailCode:"",
          email:""
        },
        UserOldMobileForm: {
          mobileCode: ""
        },
        UserNewMobileForm1: {
          mobile:""
        },
        UserNewMobileForm2: {
          mobile:"",
          mobileCode: ""
        },
        UserSecretForm: {
          newPassword: "",
          oldPassword: "",
          checkPassword:""
        },
        rules: {
          mobile: [
            { required: true, message: "手机不能为空", trigger: "blur" },
            { validator:validateMobilePhone, trigger: "blur" }
          ],
          email: [
            { required: true, message: "邮箱不能为空", trigger: "blur" },
            { validator:validateEmail, trigger: "blur" }
          ],
          mobileCode: [
            { required: true, message: "验证码不能为空", trigger: "blur" },
          ],
          emailCode: [
            { required: true, message: "验证码不能为空", trigger: "blur" },
          ],
          newPassword: [
            { required: true, message: "密码不能为空", trigger: "blur" },
            { min: 6, max: 30, message: "长度在 6 到 30 个字符", trigger: "blur" }
          ],
          oldPassword: [
            { required: true, message: "密码不能为空", trigger: "blur" },
            { min: 6, max: 30, message: "长度在 6 到 30 个字符", trigger: "blur" }
          ],
          checkPassword: [
            { required: true, message: "确认密码不能为空", trigger: "blur" },
            {
              min: 6,
              max: 30,
              message: "长度在 6 到 30 个字符",
              trigger: "blur"
            },
            { validator: validatePass, trigger: "blur" }
          ]
        },
        password:{
          value: 1
        }
      }
    },
    created(){
      this.getMycount();
      this.getUser();
      this.getContacts();
    },
    computed: {
      fileUploadUrl() {
        return this.$global.baseUrl + '/user/upload-image';
      },
      requestHeader() {
        return { 'user-center-token':Cookies.get('user-center-token')}
      },
    },
    methods:{
      getMycount:function(){
        let self=this;
        let date=new Date();
        if(date.getHours()>=0&&date.getHours()<12){
          self.hoursTip="上午好"
        }else if(date.getHours()>=12&&date.getHours()<18){
          self.hoursTip="下午好"
        }else{
          self.hoursTip="晚上好"

        }

      },
      //...mapMutations(['setHeaderObjectToken']),
      dateFrm: (e) =>{
        // console.log(e);
        return moment(e).format('YYYY-MM-DD');
      },
      //点击添加dom节点
      addExceptPrice() {
        //console.log(this.ele);
        if(this.ele <5){
          this.ele ++;
          this.items2.index = this.ele;
          this.items2.phone ="";
          this.items2.title ="";
          this.contactsForm.contacts.push(this.items2);
         // console.log(this.contactsForm.contacts);
        }else {
          this.$message({
            message: "最多只能添加5组联系方式！",
            type: "warning"
          });
        }
      },
      //添加联系方式
      onSubmitContacts() {
            const form = this.contactsForm;
            //console.log(form);
            addContact(form).then((res) => {
              if (res.data.code === this.$global.successCode) {
                //console.log("添加联系方式成功",res);
                // 操作成功
                this.$message({
                  message: "添加联系方式成功！",
                  type: "success"
                });
                this.getContacts();
              }
            }, function (error) {
              console.log("添加联系方式失败",error);
            });
      },
     //删除一组联系方式
      deleteExceptPrice(index1,index2,Ttitle) {
        if(Ttitle != ""){
          this.$confirm('是否确定删除该组联系方式', '提示', {confirmButtonText: '确定', cancelButtonText: '取消', type: 'warning'}).then(() => {
            deleteContact({index: index2}).then((res) => {
              if (res.data.code === this.$global.successCode) {
                //console.log("删除联系方式数据成功",res);
                this.$message({
                  message: "删除联系改组联系方式成功！",
                  type: "success"
                });
                this.ele --;
                this.contactsForm.contacts.splice(index1, 1);
                //console.log(this.ele);
              }

            }, function (error) {
              console.log("删除联系方式数据失败",error);
            });
          }).catch(() => {});
        }else {
           this.ele --;
          this.contactsForm.contacts.splice(index1, 1);
          //console.log(this.ele);
        }
      },
      //获取用户列表
      getUser: function () {
        getUserList().then((res) => {
          if (res.data.code === this.$global.successCode) {
            //console.log("请求所有基本资料数据成功",res);
            this.users = res.data.data;
            this.users.userPosts = res.data.data.userPosts;
          }
        }, function (error) {
          console.log("请求所有基本资料数据失败",error);
        });
      },
      //获取用户联系方式
      getContacts: function () {
        getContactList().then((res) => {
          if (res.data.code === this.$global.successCode) {
            //console.log("请求联系方式数据成功",res);
            this.contactsForm.contacts = res.data.data;
            this.ele = this.contactsForm.contacts.length;
            //console.log(this.ele);
          }
        }, function (error) {
          console.log("请求联系方式数据失败",error);
        });
      },
      //提交修改用户名
      onSubmitUserName() {
        this.$refs['UserNameForm_rules'].validate(valid => {
          if (valid) {
            const form = this.UserNameForm;
            //console.log(form);
            //表单数据验证完成之后，提交数据;
            updateUserName(form).then((res) => {
              if (res.data.code === this.$global.successCode) {
                // console.log("请求用户名数据成功",res);
                // 操作成功
                this.$message({
                  message: "用户名修改成功！",
                  type: "success"
                });
                this.visible.updatenamepost = false;
                this.getUser();
              }
            }, function (error) {
              console.log("请求用户名数据失败",error);
            });
          }
        });
      },
      //修改头像
      onSubmitImage() {
        this.getUser();
          // 操作成功
          this.$message({
            message: "头像修改成功！",
            type: "success"
          });
        this.visible.updateImagepost = false;
      },
      handleAvatarSuccess(res, file) {
       this.imageUrl = res.data;
       //console.log(res.data);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      },

      changePwd(value){
        if(/[a-zA-Z]+/.test(value) && /[0-9]+/.test(value) && /\W+\D+/.test(value)) {
          this.password.value = 100;
          this.psdmegtxt = "强";
        }else if(/[a-zA-Z]+/.test(value) || /[0-9]+/.test(value) || /\W+\D+/.test(value)) {
          if(/[a-zA-Z]+/.test(value) && /[0-9]+/.test(value)) {
            this.password.value = 60;
            this.psdmegtxt = "中";
          }else if(/\[a-zA-Z]+/.test(value) && /\W+\D+/.test(value)) {
            this.password.value = 60;
            this.psdmegtxt = "中";
          }else if(/[0-9]+/.test(value) && /\W+\D+/.test(value)) {
            this.password.value = 60;
            this.psdmegtxt = "中";
          }else{
            this.password.value = 30;
            this.psdmegtxt = "弱";
          }
        }
      },

      //重置密码
      onSubmitSecret() {
        this.$refs['UserSecretForm'].validate(valid => {
          if (valid) {
            //this.UserSecretForm.newPassword = this.inputPwd;
            const form = this.UserSecretForm;
            //console.log(form);
            //表单数据验证完成之后，提交数据;
            updateSecret(form).then((res) => {
              if (res.data.code === this.$global.successCode) {
                //console.log("请求用密码数据成功",res);
                // 操作成功
                this.$message({
                  message: "密码修改成功！",
                  type: "success"
                });
                this.visible.updatesecretpost = false;
              }

            }, function (error) {
              console.log("请求密码数据失败",error);
            });
          }
        });
      },
      // 请求旧手机验证码
      getOldMobileCode() {
        sendOldMobileCode().then((res) => {
         // if (res.data.code === this.$global.successCode) {
            //console.log("请求旧手机验证码成功",res);
            this.sendAuthCode = false;
            //设置倒计时秒
            this.auth_time = 60;
            var auth_timetimer = setInterval(()=>{
              this.auth_time--;
              if(this.auth_time<=0){
                this.sendAuthCode = true;
                clearInterval(auth_timetimer);
              }
            }, 1000);
          // }else{
          //   console.log("==================4")
          // }

        }, function (error) {
          
          console.log("请求旧手机验证码失败",error);
        })

      },
      // 验证原手机验证码
      validateOldMobileCodes() {
             this.$refs['UserOldMobileForm'].validate(valid => {
              if (valid) {
                const form = this.UserOldMobileForm;
               //console.log(form);
                //表单数据验证完成之后，提交数据;
                validateOldMobileCode(form).then((res) => {
                  if (res.data.code === this.$global.successCode) {
                    //console.log("验证原手机验证码成功",res);
                    this.visible.updatemobilepost = false;
                    this.visible.updatemobilepost1 = true;
                    // console.log(this.users.email);
                  }

                }, function (error) {
                  console.log("验证原手机验证码失败",error);
                })
              }
            });
      

      },
      // 请求新手机验证码
      getNewMobileCode:function () {
        this.$refs['UserNewMobileForm1'].validate(valid => {
          if (valid) {
            const form = this.UserNewMobileForm1;
            sendNewMobileCode(form).then((res) => {
              //if (res.data.code === this.$global.successCode) {
                //console.log("请求新手机验证码成功",res);
                this.sendAuthCode1 = false;
                //设置倒计时秒
                this.auth_time1 = 60;
                var auth_timetimer = setInterval(()=>{
                  this.auth_time1--;
                  if(this.auth_time1<=0){
                    this.sendAuthCode1 = true;
                    clearInterval(auth_timetimer);
                  }
                }, 1000);
              //}

            }, function (error) {
              console.log("请求新手机验证码失败",error);
            })

          }
        });

      },
      //修改绑定手机
      onSubmitMobile() {
        this.$refs['UserNewMobileForm2'].validate(valid => {
          if (valid) {
            this.UserNewMobileForm2.mobile = this.UserNewMobileForm1.mobile;
            const form = this.UserNewMobileForm2;
            //console.log(form);
            //表单数据验证完成之后，提交数据;
            updateMobile(form).then((res) => {
              if (res.data.code === this.$global.successCode) {
                // console.log("请求手机号码数据成功",res);
                // 操作成功
                this.$message({
                  message: "绑定手机修改成功！",
                  type: "success"
                });
                this.visible.updatemobilepost = false;
                this.visible.updatemobilepost1 = false;
                this.getUser();
              }

            }, function (error) {
              console.log("请求手机号码数据失败",error);
            });
          }
        });
      },
      // 请求邮箱验证码
      getEmailCode:function () {
        this.$refs['UserEmailForm1'].validate(valid => {
          if (valid) {
            const form =this.UserEmailForm1;
            // console.log(form);
            sendEmailCode(form).then((res) => {
              if (res.data.code === this.$global.successCode) {
                //console.log("请求邮箱验证码成功",res);
                this.sendAuthCode2 = false;
                //设置倒计时秒
                this.auth_time2 = 60;
                var auth_timetimer = setInterval(()=>{
                  this.auth_time2--;
                  if(this.auth_time2<=0){
                    this.sendAuthCode2 = true;
                    clearInterval(auth_timetimer);
                  }
                }, 1000);
              }

            }, function (error) {
              console.log("请求邮箱验证码失败",error);
            })

          }
        });

      },
      //修改绑定邮箱
      onSubmitEmail() {
        this.$refs['UserEmailForm2'].validate(valid => {
          if (valid) {
            this.UserEmailForm2.email = this.UserEmailForm1.email;
            const form = this.UserEmailForm2;
            // console.log(form);
            //表单数据验证完成之后，提交数据;
            updateEmail(form).then((res) => {
              if (res.data.code === this.$global.successCode) {
                //console.log("请求邮箱账号成功",res);
                // 操作成功
                this.$message({
                  message: "绑定邮箱修改成功！",
                  type: "success"
                });
                this.visible.updateemailpost = false;
                // console.log(this.users.email);
              }

            }, function (error) {
              console.log("请求邮箱账号失败",error);
            })
          }
        });
      }

    }
  }
</script>

<style scoped>
  .el-form{width: 100%;}
.avatar-uploader .el-upload{border:1px dashed #d9d9d9;border-radius:6px;cursor:pointer;position:relative;overflow:hidden}
.avatar-uploader .el-upload:focus,.avatar-uploader .el-upload:hover{border-color:#409eff}
.avatar-uploader .avatar-uploader-icon{border:1px dashed #d9d9d9;border-radius:6px;cursor:pointer;font-size:28px;color:#8c939d;width:178px;height:178px;line-height:178px;text-align:center}
.avatar-uploader .avatar{width:178px;height:178px;display:block}
.el-input {position: relative;font-size: 14px;display: inline-block;}
.el-form-item__error {position: relative;}
.iconremove{font-size: 20px;color: rgb(0, 170, 238);margin-left: 5px;cursor: pointer;}
.auth_text_blue{color: #fff;background: #00aaee;    border: 1px solid #00aaee;transition: .1s;font-weight: 500;padding:11px 18px;font-size: 12px;border-radius: 4px; cursor: pointer}
</style>




